body {
  font-family: 'AcuminPro';
  background-color: #000;
}

p,
a,
h1 {
  color: white;
}
h1 {
  font-family: 'Acumin Pro';
  font-weight: 800;
}
p,
a {
  font-family: 'Bilo';
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
}

#logo {
  /* position: absolute; */
  height: 30px;
  /* height: 16px; */
  padding: calc(min(1vmax, 15px) - (30px - 19px) / 2);
  /*1vmax = 1vmax margin-top of menu + 0.5vmax*2 padding (top+bottom) of menu*/
  background-color: #cddd51;
}

.menu {
  display: flex;
}
.menu a,
.menu p {
  font-family: 'Acumin Pro';
  font-weight: 400;
  /* background-color: rgba(255, 255, 0, 1); */
  margin: min(1vmax, 15px);
  text-decoration: none;
  padding: min(0.5vmax, calc(15px / 2));
  background: linear-gradient(
    to top,
    #cddd5100 25%,
    #cddd51 25% 50%,
    #cddd5100 50%
  );
  background-position: left top;
  background-size: 100% 270%;
  transition: all 0.2s linear;
  animation: all 0.2s linear;
}
.menu .current {
  color: white;
  /* text-decoration: underline solid #CDDD51; */
  border-bottom: 2px solid#CDDD51;
}

.menu a:visited {
  /* background-color: rgba(255, 255, 0, 1); */
  color: white;
}

.menu a:hover {
  /* background: linear-gradient(to top, #CDDD5100 20%, #CDDD51 20% 70%, #CDDD5100 50%); */
  background-position: left bottom 30%;
  color: black;
  /* background-color: #CDDD51; */
}

.flex .centre {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  align-content: center;
  flex-wrap: wrap;
}
.flex h1 {
  font-size: 30px;
  margin-bottom: 0;
  margin-top: 2vh;
}
.italics {
  font-style: italic;
}
.tagline {
  margin-top: 0;
  font-size: 20px;
}

.flex .seperator {
  /* margin: 0 15vw; */
  height: 10px;
  border-radius: 2px;
  background: #111111;
}
.article p.last {
  margin-bottom: 0;
  padding-bottom: 0;
}
@media (min-aspect-ratio: 6/10) {
  .menu {
    justify-content: center;
    align-items: flex-start;
  }
  #logo {
    margin-right: 1vmax;
  }
  .flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: flex-start;
    margin: 2.5vh 0;
  }
  .flex .seperator {
    min-width: 525px;
    width: 60vw;
    max-width: calc(1000px + 5vw);
  }
  #caricature {
    max-width: 300px;
    width: 25vw;
    margin-right: 5vw;
  }

  .flex .article {
    min-width: 500px;
    width: 35vw;
    max-width: 650px;
  }
}
@media (max-aspect-ratio: 6/10) {
  .menu {
    width: 100vw;
    position: absolute;
    bottom: 5vh;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    align-content: center;
  }
  .flex {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
    justify-content: center;
    margin: 2vh 0;
  }

  #caricature {
    max-width: 40vw;
  }

  .flex .article {
    min-width: 70vw;
    max-width: 85vw;
  }
}
